.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}
.form {
    width: 40%;
    margin: 20px auto;
    display: block;
    font-family: 'Noto Sans', sans-serif; /*       */
    padding: 10px 100px;
    background-color: #313338; /*    */
    border-radius: 5px; /*    */
    border: 2px solid #9FC4F0; /*   */
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/*   */

h1 {
    margin: 15px;
    padding: 0;
    text-align: center; /*    */
    color: #FFF9FC; /*    */
    font-size: 30pt;
}

/*   */

.field {
    margin: 15px 0; /*        */
}

/*     */

.field label {
    
    color: #ffffffcd; /*    */
    margin-bottom: 5px; /*    */
    display: block; /*      */
}

.valid {
color: green; /*    */   
}

.invalid {
color: red; /*    */
}

.hide {
display: none; /*   */
}

/*       */
.instrucions {
    margin-top: 5px;
    font-size: 12px;
    color: #666;
}

.offscreen {
    display: none;
}

/*     */
.errmsg {
margin-top: 5px;
font-size: 14px;
color: red;
}

/*   */

.field input {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /*        */
    width: 60%;
    outline: none; /*       */
    box-sizing: border-box; /*      */
    background-color: #1D2125; /*     */
    color: #FFF9FC; /*      */
    border-radius: 5px; /*     */
    border: 1px solid #1D2125; /*  */
}

/*        */

.field input[type="text"]:focus,
.field input[type="password"]:focus {
    border: 1px solid #4361EE; /*      */
}
/*    */
.btns {
    display: flex;
    justify-content: space-between;
    margin-top: 50px; /*    */
}

#passwordMatch {
    margin-left: 20%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.btns .confirm {
    background-color: #4361EE; /*    */
    color: white; /*     */
    border: none; /*    */
    padding: 10px; /*       */
    cursor: pointer; /*      */
    border-radius: 4px; /*    */
    width: 45%;
    max-width: 180px;
    font-size: 17px;
    font-weight: 400;
    margin-bottom: 10px;
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/*       */

.confirm:hover {
    background-color: #3349C0; /*        */
}

/*  */

.cancel {
    background-color: #8BE3FF; /*    */
    color: black; /*     */
    border: none; /*    */
    padding: 10px; /*       */
    cursor: pointer; /*      */
    border-radius: 4px; /*    */
    width: 45%;
    max-width: 180px;
    font-size: 17px;
    font-weight: 400;
    margin-bottom: 10px;
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

.cancel:hover {
    background-color:#5fb8d3;
}

/*     */

.select_container select {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /*        */
    width: 60%;
    outline: none; /*       */
    box-sizing: border-box; /*      */
    background-color: #1D2125; /*     */
    color: #FFF9FC; /*      */
    border-radius: 5px; /*     */
    border: 1px solid #1D2125; /*  */

}

.field_dropdown select:focus {
    border: 1px solid #4361EE; /*      */
}

.field_dropdown option {
    background-color: #1D2125; /*      */
    color: #FFF9FC; /*      */

}

/*       "" */

.input_form_continue input[type="submit"]:hover {
    background-color: #3349C0; /*        "" */
}

.field_dropdown {
    margin: 15px 0;
}

.field_dropdown label {
    color: #ffffffcd;
    display: block;
    margin-bottom: 5px;
}

.select_container {
    position: relative;
}

.field_dropdown select {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px;
    outline: none;
    box-sizing: border-box;
    background-color: #1D2125;
    color: #FFF9FC;
    border-radius: 5px;
    border: 1px solid #1D2125; /*  */

    
}

.field_dropdown select:focus {
    border: 1px solid #4361EE;
}

/*      */

.field_textarea textarea {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /*        */
    outline: none; /*       */
    box-sizing: border-box; /*      */
    background-color: #1D2125; /*     */
    color: #FFF9FC; /*      */
    border-radius: 5px; /*     */
    border: 1px solid #1D2125; /*  */
    resize: none; /*      */
    width: 60%;

}

.field_textarea textarea:focus {
    border: 1px solid #4361EE; /*         */
}

.field_textarea label {
    color: #ffffffcd; /*    */
    margin-bottom: 5px; /*    */
    display: block; /*      */
}

.text{
    text-align: center;
    margin-top: 20px;
    color: #FFFFFF;
    font-weight: 300;
    margin-bottom: 0;
    font-size: 15px;
}

.log_button{
    text-decoration: none;
    color:#4361EE;
}

.log_button:hover{
    color:#3349C0;
}

@media (max-width: 1200px){
    .form {
        min-width: 600px;

    }
}
@media (max-width: 700px){
    .form {
        min-width: 400px;
        width: 100%;
        padding: 10px 50px
    }
    .field input {
        width: 100%;
    }
}