.login {
     /* background: url(../../images/bg.jpg); */
     /* background-size: cover; */
     /* background-repeat: repeat; */
     display: flex; /*  flex-       */
     justify-content: center;
     align-items: center;
     height: 100vh; /*   */
     background-repeat: no-repeat;
    }

/*   */

.form {
    position: absolute; /*       */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /*      */
    font-family: 'Noto Sans', sans-serif; /*       */
    background-color: #313338; /*    */
    padding: 20px; /*    */
    border-radius: 5px; /*    */
    width: 350px; /*   */
    border: 2px solid #9FC4F0; /*   */
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/*   */

.form h1 {
    margin: 40px;
    padding: 0;
    text-align: center; /*    */
    color: #FFF9FC; /*    */
}

/*   */

.input_form {
    margin: 15px 0; /*        */
    
}

/*     */

.input_form label {
    
    color: #ffffffcd; /*    */
    margin-bottom: 5px; /*    */
    display: block; /*      */
    margin-left: 5%;
}

/*   */

.input_form .username, .input_form .input_form_password {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /*        */
    width: 90%; /*  90%   */
    outline: none; /*       */
    box-sizing: border-box; /*      */
    background-color: #1D2125; /*     */
    color: #FFF9FC; /*      */
    border-radius: 5px; /*     */
    border: 1px solid #1D2125; /*  */
    margin-left: 5%;
}

.input_form_password input, .focus_input_form_password input{
    background-color: #1D2125;
    outline: none;
    box-sizing: border-box;
    color: #FFF9FC;
    width: 90%;
    border: none;
}

.focus_input_form_password {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /*        */
    width: 90%; /*  90%   */
    outline: none; /*       */
    box-sizing: border-box; /*      */
    background-color: #1D2125; /*     */
    color: #FFF9FC; /*      */
    border-radius: 5px; /*     */
    border: 1px solid #4361EE; /*  */
    margin-left: 5%;
}

.icon{
    margin-left: 2%;
}

/*        */

.input_form .username:focus {
    border: 1px solid #4361EE; /*      */
}


/*  */

.input_form input[type="submit"] {
    margin-top: 50px; /*     */
    background-color: #4361EE; /*    */
    color: white; /*     */
    border: none; /*    */
    padding: 10px; /*       */
    cursor: pointer; /*      */
    border-radius: 4px; /*    */
    width: 50%;
    margin-left: 25%;
    margin-right: 25%;
    font-size: 15px;
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/*       */

.input_form input[type="submit"]:hover {
    background-color: #3349C0; /*        */
}

.reg_button{
    text-decoration: none;
    color:#4361EE;
}

.reg_button:hover{
    color:#3349C0;
}

.text{
    text-align: center;
    margin-top: 20px;
    color: #FFFFFF;
    font-weight: 300;
    margin-bottom: 0;
    font-size: 15px;
}


.offscreen {
    display: none;
}

.errmsg {
    margin-top: 5px;
    font-size: 14px;
    color: red;
    margin-left: 5%;
}